/* Minimal base: Tailwind's @tailwind base (in tailwind.css) handles reset and defaults.
   Only add what Tailwind does not cover. */

html,
body {
  margin: 0;
  padding: 0;
  width: 100%;
  min-height: 100%;
  background-color: #1E1E28;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  min-height: 100vh;
  background-color: #1E1E28;
}

/* Dark dropdown options for native select (where browser supports it) */
select option {
  background: #334155;
  color: #fff;
}
